@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600,700);
.loader {
  border: 2px solid #eeeeee;
  border-radius: 50%;
  border-top: 2px solid;
  width: 20px;
  height: 20px;
  display: inline-block;
  -webkit-animation: spin 1s linear infinite;
  /* Safari */
  animation: spin 1s linear infinite; }

/* Safari */
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg); } }

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.page-header {
  padding: 2rem 0;
  background: #d5f0f7; }
  .page-header h4 {
    font-size: 1.1rem;
    font-weight: bold; }

.App {
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  color: #000;
  display: -webkit-flex;
  display: flex;
  width: 100%; }

.main-content {
  width: 100%;
  min-height: 100vh; }
  .main-content .content-wrapper {
    background-color: #ffffff;
    padding: 1rem;
    border-radius: 3px;
    box-shadow: 0 0 5px #ccc; }
    .main-content .content-wrapper .item-count {
      font-size: 1rem;
      font-weight: bold;
      padding-top: 0.5rem; }

/* Override Button Syle */
.btn {
  border-radius: 0; }
  .btn.btn-brand {
    background-color: #ffff00 !important;
    border-color: #000000 !important;
    border-width: 2px; }
  .btn.btn-brand-2 {
    background-color: #d5f0f7 !important;
    border-color: #000000 !important;
    border-width: 2px; }

.input-group-text {
  border-radius: 0;
  background: #d5f0f7; }

